@import "config";
main {
  width: 100vw;
  height: 100vh;
  height: 100svh;
  display: grid;
  grid-template-rows: 90vh 1fr;
  grid-template-rows: 90svh 1fr;
  .nav--box {
    display: flex;
    width: 100vw;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    background-color: #000;
    &.hidden {
      display: none;
    }
    &-tag {
      font-family: "montserrat";
      font-size: 1rem;
      padding: 1rem;
      color: #fff;
      border: 0.1rem solid #fff;
      border-radius: 1rem;
      display: flex;
      align-items: center;
      text-align: center;
      &:active {
        color: #555;
        border-color: #555;
      }
    }
  }
  .list--container {
    @include centerGrid;
    .empty-tag {
      font-family: "montserrat";
      font-weight: 300;
      font-size: 1rem;
      font-style: italic;
    }
  }
}

.nav-container {
  @include centerGrid;
  width: 100vw;
  height: 100%;
  input {
    border-top: 0.1rem solid black;
    width: 100vw;
    height: 100%;
    padding: 1rem;
    font-size: 1.5rem;
    outline: none;
    text-align: center;
    font-family: "montserrat";
  }
}

.item {
  width: 90vw;
  margin-bottom: 1rem;
  color: #000;
  font-family: "montserrat";
  display: flex;
  align-items: center;
  // overflow-x: hidden;
  overflow-wrap: break-word;
  .checkbox {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 0.1rem solid black;
    margin-right: 1rem;
    &-complete {
      background-color: #000;
    }
  }
  h1 {
    font-weight: 300;
  }
  &-complete {
    text-decoration: line-through;
    font-style: italic;
  }
}
