* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

main {
  grid-template-rows: 90svh 1fr;
  width: 100vw;
  height: 100svh;
  display: grid;
}

main .nav--box {
  background-color: #000;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 100%;
  display: flex;
}

main .nav--box.hidden {
  display: none;
}

main .nav--box-tag {
  color: #fff;
  text-align: center;
  border: .1rem solid #fff;
  border-radius: 1rem;
  align-items: center;
  padding: 1rem;
  font-family: montserrat;
  font-size: 1rem;
  display: flex;
}

main .nav--box-tag:active {
  color: #555;
  border-color: #555;
}

main .list--container {
  justify-content: center;
  align-items: center;
  display: grid;
}

main .list--container .empty-tag {
  font-family: montserrat;
  font-size: 1rem;
  font-style: italic;
  font-weight: 300;
}

.nav-container {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100%;
  display: grid;
}

.nav-container input {
  text-align: center;
  border-top: .1rem solid #000;
  outline: none;
  width: 100vw;
  height: 100%;
  padding: 1rem;
  font-family: montserrat;
  font-size: 1.5rem;
}

.item {
  color: #000;
  overflow-wrap: break-word;
  align-items: center;
  width: 90vw;
  margin-bottom: 1rem;
  font-family: montserrat;
  display: flex;
}

.item .checkbox {
  border: .1rem solid #000;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  margin-right: 1rem;
}

.item .checkbox-complete {
  background-color: #000;
}

.item h1 {
  font-weight: 300;
}

.item-complete {
  font-style: italic;
  text-decoration: line-through;
}

